<template>
  <div class="admin-container">
    <div v-if="loading" class="loader"></div>
    <h1>لوحة التحكم</h1>
    <ag-grid-vue
      :columnDefs="columnDefs"
      :rowData="requests"
      :domLayout="'autoHeight'"
      :gridAutoHeight="true"
      class="ag-theme-quartz custom-ag-grid"
      :enableRtl="true"
      :rowHeight="40"
    >
    </ag-grid-vue>
  </div>
</template>

<script>
import "ag-grid-community/styles/ag-grid.css" // Mandatory CSS required by the grid
import "ag-grid-community/styles/ag-theme-quartz.css" // Optional Theme applied to the grid
import { AgGridVue } from "ag-grid-vue3" // AG Grid Component
import axios from "axios"
import qs from "qs"
import { serverUrl } from "../const"
export default {
  components: {
    AgGridVue,
  },
  data() {
    return {
      requests: [],
      loading: true,
      columnDefs: [
        { field: "id", headerName: "رقم", valueGetter: "node.rowIndex + 1" }, // Colonne d'ID commençant par 1
        { field: "name", headerName: "الاسم", sortable: true },
        { field: "phoneNumber", headerName: "رقم الهاتف", sortable: true },
        {
          headerName: "إرسال",
          cellRenderer: (params) => {
            if (params.data?._id !== null) {
              const div = document.createElement("div")
              div.classList.add("btn-group")
              div.innerHTML = `
                  <input
                     className="upload__inputfile d-none"
                     type="file"
                     multiple=""
                     data-max_length="20"
                    name="image"
                    accept="image/*"
                    />
            `
              this.addClickEventListener(div, params.data)
              return div
            } else {
              return ""
            }
          },
        },
        {
          headerName: "الصورة",
          cellRenderer: (params) => {
            if (params.data?.urlPicture) {
              const div = document.createElement("div")
              div.classList.add("btn-group")
              div.innerHTML = `
                  <a href="${serverUrl}/api/admin/picture/${params.data?._id}/${params.data?.urlPicture}" target="_blank" class="btn btn-info">فتح الصورة</a>`
              return div
            } else {
              return ""
            }
          },
        },
        {
          field: "creationDate",
          headerName: "تاريخ الإنشاء",
          valueFormatter: (params) => {
            const date = new Date(params.value)
            const formattedDate = `${
              date.getMonth() + 1
            }/${date.getDate()}/${date.getFullYear()} ${date.getHours()}:${date.getMinutes()}:${date.getSeconds()}`
            return formattedDate
          },
        }, // Colonne de date de création
        {
          field: "sendDate",
          headerName: "تاريخ الإرسال",
          valueFormatter: (params) => {
            const date = new Date(params.value)
            const formattedDate = `${
              date.getMonth() + 1
            }/${date.getDate()}/${date.getFullYear()} ${date.getHours()}:${date.getMinutes()}:${date.getSeconds()}`
            return formattedDate
          },
        },
      ],
    }
  },
  methods: {
    async takePhoto(event, data) {
      this.loading = true
      const idOrder = data?._id
      const phoneNumber = data?.phoneNumber

      try {
        const file = event.target.files[0]
        if (file) {
          /* const reader = new FileReader()
          reader.readAsDataURL(file)
          reader.onload = async () => {
            const imageDataUrl = reader.result
            const response = await axios.post(
              `${serverUrl}/api/admin/uploadPhoto`,
              {
                imageData: imageDataUrl,
                orderId: idOrder,
              }
            )
            const imageUrl = response.data?.imageUrl
            await this.sendPicture(imageUrl, phoneNumber)
            //alert("Photo enregistrée avec succès !")
          }*/
          const formData = new FormData()
          formData.append("imageData", file) // Ajoutez le fichier à FormData

          // Ajoutez d'autres données à envoyer avec le fichier
          formData.append("orderId", idOrder)

          const response = await axios.post(
            `${serverUrl}/api/admin/uploadPhoto`,
            formData, // Utilisez FormData comme corps de la requête
            {
              headers: {
                "Content-Type": "multipart/form-data", // Définissez le type de contenu sur multipart/form-data pour envoyer des fichiers
              },
            }
          )
          const imageUrl = response.data?.imageUrl
          await this.sendPicture(imageUrl, phoneNumber)
          await axios.post(`${serverUrl}/api/admin/update-send-date/${idOrder}`)
          this.loading = false
        }
        // Code utilisant une alternative pour les ordinateurs de bureau
      } catch (error) {
        this.loading = false
        console.error("Erreur lors de la prise de la photo :", error)
      }
    },
    addClickEventListener(element, data) {
      element.addEventListener("change", (event) => this.takePhoto(event, data))
    },
    async fetchRequests() {
      try {
        const response = await axios.get(`${serverUrl}/api/admin/requests`)
        console.log("response", response)
        this.requests = response.data

        this.loading = false
      } catch (error) {
        this.loading = false
        console.log("errer", error.message)
      }
    },
    async sendPicture(url, phoneNumber) {
      var data = qs.stringify({
        token: "j290mey56mbdrzo3",
        to: phoneNumber,
        image: url,
        caption: "جزاكم الله خيرًا ، ونسأل الله أن يجعل ذلك في ميزان حسناتكم.",
      })
      var config = {
        method: "post",
        url: "https://api.ultramsg.com/instance81883/messages/image",
        headers: {
          "Content-Type": "application/x-www-form-urlencoded",
        },
        data: data,
      }

      axios(config)
        .then(function (response) {
          console.log(JSON.stringify(response.data))
          alert("تم التقاط الصورة وإرسالها إلى رقم الواتساب!")
        })
        .catch(function (error) {
          console.log(error)
        })
    },
  },
  mounted() {
    this.fetchRequests()
  },
}
</script>

<style scoped>
.admin-container {
  text-align: right;
  margin: 20px;
}

.btn-group {
  display: flex;
  gap: 10px;
}
.loader {
  border: 16px solid #f3f3f3; /* Gris clair */
  border-top: 16px solid #3498db; /* Bleu */
  border-radius: 50%;
  width: 120px;
  height: 120px;
  animation: spin 2s linear infinite;
  margin: auto; /* Centrer le loader horizontalement */
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
/* Styles spécifiques à Ag-Grid */
.custom-ag-grid {
  width: 100%;
  height: 100%;
}

@media screen and (max-width: 768px) {
  .custom-ag-grid {
    max-width: 80%;
    margin: 0 auto;
  }
  .ag-row {
    height: 30px; /* Réduire la hauteur des lignes pour les appareils mobiles */
  }
}

@media screen and (max-width: 480px) {
  .custom-ag-grid {
    max-width: 90%;
    margin: 0 auto;
  }
}
</style>
